<template>
  <div id="chart" style="width: 100%; height: 380px; min-width: 280px;"></div>
</template>

<script>
export default {
  name: "chart",
  props: ["value"],
  data() {
    return {
      mychart: null,
    };
  },
  mounted: function () {
    var chartDom = document.getElementById("chart");
    this.myChart = this.$echarts.init(chartDom);
    this.option && this.myChart.setOption(this.option);
  },
  watch: {
    value: function () {
      this.option && this.myChart.setOption(this.option);
    },
  },
  computed: {
    option: function () {
      var xs = [];
      var ys = [];
      for(var i=0;i<this.value.length;i++){
        if(this.value[i]==-1)continue;
        xs.push(i+1);
        ys.push(this.value[i]);
      }
      return {
        grid: {
          //防止标签溢出
          containLabel: true,
          left: '5%',
          right: '15%'
        },
        xAxis: {
          type: "category",
          data: xs,
          name: "Year",
          nameTextStyle : {padding: [0,0,0,-14], fontSize: 16},
          axisTick: {
            alignWithLabel: true
          }
        },
        yAxis: {
          type: "value",
          name: "Survival probability(%)",
          nameTextStyle : {padding: [4,0,8,100], fontSize: 16},
          min: 0,
          max: 100
        },
        series: [
          {
            data: ys,
            type: "line",
            smooth: true,
            label:{
              show: true,
              formatter: '{c}%'
            }
          },
        ],
      };
    },
  },
};
</script>


